<template>
  <div class="box-1">
    <div class="red">第{{n}}次</div>
     <div>
        <div class="row" >
       <!-- 给Box编一个号; 接收Box.vue($emit)传入的值,用$event -->
       <Box @click="onClickBox(0,$event)" :n="n" />
       <Box @click="onClickBox(1,$event)" :n="n" />
       <Box @click="onClickBox(2,$event)" :n="n" />
        </div>
       <div class="row">
       <Box @click="onClickBox(3,$event)" :n="n" />
       <Box @click="onClickBox(4,$event)" :n="n" />
       <Box @click="onClickBox(5,$event)" :n="n" />
       </div>
       <div class="row">
       <Box @click="onClickBox(6,$event)" :n="n" />
       <Box @click="onClickBox(7,$event)" :n="n" />
       <Box @click="onClickBox(8,$event)" :n="n" />
       </div>
     </div>
     <div>
       结果:{{ result === null ? '胜负未分':`胜方为${result}`}}
     </div>
     <!-- <div>{{map}}</div>
     <div>{{result}}</div> -->
  </div>
</template>

<script>
import Box from './Box.vue'; 

export default {
  components: { Box },
  data (){
    return { 
    n: 0, // 只要某个Box被点击了就加1
    // 记住内容
    map:[
         [null,null,null],
         [null,null,null],
         [null,null,null]
        ],
        result: null // 没赢
    }; 
  },
  methods: {
    onClickBox(i,text) {
      console.log(`${i}号被点击;内容是:${text}`);
      /*
      i map中的位置
      0 map[0][0]
      1 map[0][1]
      2 map[0][2]
      3 map[1][0]
      4 map[1][1]
      5 map[1][2]
      6 map[2][0]
      7 map[2][1]
      8 map[2][2]
      */
      this.map[Math.floor(i/3)][i%3] = text;
      // console.log('Box被点击了')
      this.n = this.n + 1;
      this.tell();
    },
    tell() {
      const map = this.map;
      // 行判断
      for(let i =0;i<2;i++){       
         if( map[i][0] !== null && map[i][0]===map[i][1] && map[i][1] === map[i][2]){
          this.result = map[i][0];
         }
      }
      // 列判断
      for(let j = 0;j<2;j++){
        if (map[0][j] !== null && map[0][j] === map[1][j] && map[1][j]=== map[2][j]) {
            this.result = map[0][j];
        }
      }
      // 斜 判断
      if(map[0][2] !==null && 
         map[0][2] === map[1][1] && 
         map[1][1] === map[2][0]){
         this.result = map[0][2];
      }
    }
  }
};
</script>

