  .row {
    display: flex;
  }
  .box-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
 .box-1 > div:first-child{
    margin: 10px 0;
    color: blue;
    font-weight: 700;
  }
 .box-1 > div:last-child {
   color: red;
   font-weight: 700;
   margin: 10px 0;
 }
</style>
