<template>
<div>
  <!-- {{ n }} -->
    <div class="box" v-on:click="onClickSelf">
    <template v-if="a">{{text}}</template>
    <template v-else></template>
    </div>
</div>
</template>

<script>
export default {
  props: ["n"], // 接收app(外面)传进来的n 
  data(){
   return {a: false, text: ""};
 },
  methods:{
   onClickSelf() {
     // 如果text不等于上面"" 说明变过了 不在点击
     if(this.text !== ""){
       return;
     }
     this.a = true;
     this.text = this.n % 2 === 0 ? 'X' :'O'; //确定text的值 n除2 等于0 就是X 否则是O
     this.$emit("click",this.text); //1 通知被点击了 app.vue监听click就会触发;并告诉外面传的值是X，O
   }
 }
};  
</script>

<style>
 .box {
   border: 1px solid black;
   width: 100px;
   height: 100px;
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 50px;
   background-color: cyan;
 }
</style>